.popup {
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99999;
  display: none;
}
.popup__popup-dialog {
  position: relative;
  z-index: 1;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  max-width: 560px;
  overflow: auto;
  background: rgba(255, 255, 255, 0.9);
  padding: 56px 56px 36px 56px;
}
@media (max-width: 1023px) {
  .popup__popup-dialog {
    font-size: 16px;
    padding: 40px 24px 40px 24px;
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
}
.popup h3 {
  margin-top: 0;
  margin-bottom: 12px;
}
.popup .gform_wrapper.gravity-theme input[type=text], .popup .gform_wrapper.gravity-theme input[type=email], .popup .gform_wrapper.gravity-theme input[type=tel] {
  background-color: white;
  padding: 11px 16px;
  font-size: 18px;
  outline: none;
  border: none;
  line-height: 26px;
  color: var(--wp--preset--color--text);
  border-radius: 0;
